body{
    background-color: #14365d;
    width: 100%;
    height: 100vh;
    display: grid;
    align-items: center;
  }
  
  form {
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    width: 500px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  form h4 {
    text-align: center;
    color: white;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
    color: orange;
  }
  
  form input[type="text"],
  form input[type="number"],
  form input[type="email"], 
  form input[type="password"] {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    margin-bottom: 10px;
    background: #14365d;
    color: white;
    border-radius: 5px;
  }

  form input[type="submit"] {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    margin-bottom: 10px;
    background: orange;
    color: black;
    border-radius: 5px;
  }